
import React from 'react';
import Playground from './Playground';
import Layout from '../../components/Layout';

async function action({ fetch, store, query }) {

  const sample = query.sample || 'sum'
  return {
    chunks: ['play'],
    title: 'Play',
    component: (
      <Layout isHeaderHidden={true}>
        <Playground {...{sample}}/>
      </Layout>
    ),
  }
}

export default action



