export default {
    sum: {
        code: 
`
const sum = (a, b) => {
    return a + b;
}`      ,
        testCode: `
describe('Math', () => {
    const sumFn = parsed.find('sum');
    it('should add 2 numbers', () => {
        const result = sumFn
                .callWith(1,2)
                .result;
        chai.expect(result).to.equal(3)
    })
})          
`
    },
    thrower: {
        code: 
`
const thrower = () => {
    const foo
    return foo.bar  
}
`      ,
        testCode: 
`
describe('thrower()', () => {
    it('should throw', () => {
        const e = parsed
                    .find('thrower')
                    .callWith()
                    .exception
        expect(!!e).to.equal(true)
    });
})          
`
    },
    async1: {
        code: 
`
const sumAsync = async (a, b) => {
    return await new Promise((resolve, reject) => {
        setTimeout(function () {
          resolve(a + b)
        }, 500)
    })
}
`,
        testCode:
`
describe('sumAsync()', () => {
    it('should return the sumAsync of two numbers', async () => {
        const result = await parsed
                                .find('sumAsync')
                                .callWith(5, 7)
                                .result
        expect(result).to.equal(12)
    });
})
`             
    }
}



