import React from 'react';
import samples from '../../lib/code-samples'

import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Editor.css';
// 
import _ from 'lodash'

class Editor extends React.Component {
  
  constructor (props) {
    super(props)
    this.state = {
      keys: Object.keys(samples),
      currentKeyIndex: 0
    }
  }

  componentDidMount () {
    const CodeFlask  = require('codeflask')
    this.flask = new CodeFlask.default(this.textInput, { 
      language: 'js',
      lineNumbers: true
    })
    this.flask.updateCode(this.props.code)

    if (this.props.handleCodeChange) {
      this.flask.onUpdate(this.props.handleCodeChange)
      // setInterval(() => {
      //   // console.log('Setting')
      //   this.props.handleCodeChange(this.flask.getCode())
      // }, 5000)  
    }
  }

  next () {
    let newIndex = (this.state.currentKeyIndex + 1) % this.state.keys.length
    this.setState({
      currentKeyIndex: newIndex
    })
  }

  render () {
    return (
      <div style={{...this.props.style}}>
        <div ref={(input) => { this.textInput = input}}></div>
      </div>
    );
  }
}

export default withStyles(s)(Editor);



