import React from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Playground.css';

import Editor from '../../components/Editor/Editor';

import { parseStr } from 'mockoff'
import CodeSamples from '../../lib/code-samples'

class Playground extends React.Component {
  constructor (props) {
    super(props)
    
    this.state = {
      code: CodeSamples[this.props.sample].code,
      testCode: CodeSamples[this.props.sample].testCode,
      codeFragment: null
    }
    this.handleCodeChange = this.handleCodeChange.bind(this)
    this.handleTestChange = this.handleTestChange.bind(this)
    this.handleRunTest = this.handleRunTest.bind(this)
  }

  componentDidMount () {
    this.vm = require('vm')
  }

  handleCodeChange (code) { 
    this.setState({
      code
    })
  }

  handleTestChange (testCode) {
    this.setState({
      testCode
    })
  }

  handleRunTest () {
    const {testCode, code} = this.state
    try {
      const parsed = parseStr(this.state.code)
      window.parsed = parsed
      const runStr = `
        mocha.setup('bdd');
        mocha.checkLeaks();
        ${this.state.testCode}
        mocha.run();
      `
      // console.log(runStr)
      this.iframeRef.contentWindow.location.reload()
      this.iframeRef.contentWindow.document.write(
        `
          <html>
            <head>
              <meta charSet="utf-8" />
              <meta httpEquiv="x-ua-compatible" content="ie=edge" />
              <link href="https://unpkg.com/mocha@4.0.1/mocha.css" rel="stylesheet" />
              <script src="https://unpkg.com/chai@4.1.2/chai.js"></script>
              <script src="https://unpkg.com/mocha@4.0.1/mocha.js"></script>
            </head>
            <body
              <div id="mocha">

              </div>
              <script>
                parsed = window.parent.parsed    
                expect = chai.expect
                should = chai.should
                assert = chai.assert            
                ${runStr}
              </script>
            </body>
          </html>
        `
      )
      // let outputStr = ''
      
      // this.vm.runInNewContext(runStr, {parsed, sinon, mocha, chai})
      // console.log(`${outputStr}`, '<<< OUTPUT')
      // this.setState({
      //   outputStr: `${outputStr}`
      // })
    } catch (e) {
      // this.setState({
      //   outputStr: `${e}`,
      // })
      // console.log(e)
    }    
  }

  render() {

    return <div style={{display: 'flex', flexDirection: 'row', justifyContent: 'space-around', width: '100vw', height: '100vh'}}>
        <div style={{flex: 1, display: 'flex', flexDirection: 'column'}}>
          <Editor {...{style: {margin: 1, flex: 1, position: 'relative'}, code: this.state.code, handleCodeChange: this.handleCodeChange}}/>
          <div style={{margin: 1, flex: 1, backgroundColor: '#424242'}}>
            <iframe style={{width: '100%', height: '100%'}} ref={(input) => { this.iframeRef = input; }}></iframe>
          </div>  
        </div>
        <div style={{flex: 1, display: 'flex', flexDirection: 'column'}}>
          <Editor {...{style: {margin: 1, flex: 3, position: 'relative'}, code: this.state.testCode, handleCodeChange: this.handleTestChange}}/>       
          <div style={{margin: 1,  backgroundColor: '#424242', flex: 1, display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
              <button onClick={this.handleRunTest}>{'Run Test'}</button>
          </div>
        </div>
    </div>
  }
}

export default withStyles(s)(Playground);



